import { type FC, useCallback, useEffect, useMemo } from 'react';
import { type ApolloError } from '@apollo/client';
import * as Sentry from '@sentry/react';
import { t } from 'i18next';
import {
  useSearchParams,
  useNavigate,
  useLocation,
  useParams,
} from 'react-router-dom';
import { Spacer, Stack } from '@/layout';
import { useMarkNoumAsRecentMutation } from '@/apollo/graphql';
import { useBreakpoints, useLaunchDarkly, useToast } from '@/hooks';
import { useSkeletonIsLoadingContext } from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import { Spinner } from '@/components/Spinner';
import { useEvents } from '@/features/events/hooks';
import { cleanList } from '@/utils/list';
import {
  ElementTypeEnum,
  SpaceTypeEnum,
  type NoumLayoutSection,
} from '@/apollo/generated/types';
import { useNoumContext } from './ChamberProvider';
import { BodyContainer } from './styles';
import {
  NoumSectionContainer,
  NoumSectionLayout,
} from '../components/NoumSections/Styles';
import { ColumnContainer } from '../components/SectionElementRearrange/styles';
import NoumViewElement from './NoumViewElement';
import {
  clearSectionWithNoTools,
  filterToolsWithSingleColumn,
} from './noumViewHelper';
import { useGetQuickQuestionHelper } from '../components/elements/QuickQuestionsElement/hooks/useGetQuickQuestionHelper';
import { RiseApplicationReadMe } from '../components/RiseApplicationReadMe';
import { RiseEssayQuestionV2 } from '../components/RiseApplicationQuestion/RiseEssayQuestionV2';

export const NoumBody: FC = () => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const {
    space,
    isArchived,
    loading,
    loadingSpace,
    sections,
    isOwner,
    isConnected,
    noumType,
    isFollowing,
  } = useNoumContext();
  const navigate = useNavigate();
  const location = useLocation();
  const [markNoumAsRecent] = useMarkNoumAsRecentMutation();
  const { isSmallerThanLaptop } = useBreakpoints();

  const { id: spaceId } = useParams();

  const { eventsCount } = useEvents({
    chamberId: space?._id || '',
    preventGetEvents: false,
  });

  const { totalQuestionCount } = useGetQuickQuestionHelper(
    spaceId || '',
    undefined,
    true,
    0,
  );

  const { addToast } = useToast();

  const [searchParams] = useSearchParams();
  const elementId = searchParams.get('elementId');

  const isCompleteLoading = useMemo(
    () =>
      !loading &&
      !loadingSpace &&
      !!space?._id &&
      document.querySelectorAll<HTMLSpanElement>(`[data-testid="spinner"]`)
        .length < 1,
    [loading, loadingSpace, space?._id],
  );

  useEffect(() => {
    if (elementId && elementId !== '') {
      if (isCompleteLoading) {
        const element = document.getElementById(elementId);
        if (element) {
          setTimeout(() => {
            element.scrollIntoView({ block: 'center', behavior: 'smooth' });
          }, 3000);
        }
      }
    } else
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
  }, [elementId, isCompleteLoading, loading, loadingSpace]);

  const handleError = useCallback(
    (error: ApolloError) => {
      const { networkError, graphQLErrors } = error;
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${
          error ? error.message : networkError
        }`,
      );
      const [err] = graphQLErrors;
      Sentry.captureException(new Error(err?.message ?? networkError), {
        tags: {
          section: 'markNoumAsRecent',
        },
      });
    },
    [addToast],
  );

  const handleMarkNoumasRecent = useCallback(
    async (noumId) => {
      await markNoumAsRecent({
        variables: { noumId },
        onError: (error) => {
          handleError(error);
        },
      });
    },
    [handleError, markNoumAsRecent],
  );
  useEffect(() => {
    if (noumEditor2) {
      navigate(
        { pathname: location.pathname, search: `?new=true` },
        { replace: true },
      );
    }
  }, [location.pathname, navigate, noumEditor2]);

  useEffect(() => {
    if (spaceId) handleMarkNoumasRecent(spaceId);
  }, [handleMarkNoumasRecent, spaceId]);

  const { isLoading } = useSkeletonIsLoadingContext();

  const sectionWithEventTool = useCallback(() => {
    if (!sections) return [];
    return sections.reduce((accumulator: NoumLayoutSection[], section) => {
      const shouldIncludeSections = filterToolsWithSingleColumn(
        section,
        eventsCount,
        totalQuestionCount,
        isConnected,
        isFollowing,
      );
      if (shouldIncludeSections) return accumulator;
      const filteredSection = {
        ...section,
        columns: section.columns.map((column) => ({
          ...column,
          tools: column.tools.filter((tool) =>
            tool.elementType === ElementTypeEnum.Calendar
              ? eventsCount > 0
              : tool.elementType === ElementTypeEnum.QuickQuestions
              ? totalQuestionCount > 0 && (isConnected || isFollowing)
              : true,
          ),
        })),
      };
      accumulator.push(filteredSection);

      return accumulator;
    }, []);
  }, [eventsCount, isConnected, isFollowing, sections, totalQuestionCount]);

  const finalSectionList = useMemo(
    () =>
      cleanList(
        isOwner
          ? clearSectionWithNoTools(sections)
          : clearSectionWithNoTools(sectionWithEventTool()),
      ),
    [isOwner, sectionWithEventTool, sections],
  );

  if (isLoading) return <Spinner />;

  return (
    <BodyContainer disabled={isArchived}>
      <>
        <Spacer height={16} />

        <Stack vertical gap={12} fullWidth>
          {noumType === SpaceTypeEnum.RiseApplication && (
            <>
              <RiseApplicationReadMe />
              <RiseEssayQuestionV2 />
            </>
          )}
          {finalSectionList.map((section) => (
            <NoumSectionContainer
              key={section._id}
              isBackground={section.background}
            >
              <NoumSectionLayout
                isSmallerThanLaptop={isSmallerThanLaptop}
                noumSectionType={section.type}
                sectionAlign={section.columnsVerticalAlignType}
              >
                {section?.columns.map((col) => (
                  <ColumnContainer
                    data-testid="noum-section-column"
                    id={col._id}
                    key={col._id}
                    gap={12}
                    vertical
                    isBackground={col.background}
                  >
                    <NoumViewElement
                      tools={col.tools}
                      isCompleteLoading={isCompleteLoading}
                      elementId={elementId}
                    />
                  </ColumnContainer>
                ))}
              </NoumSectionLayout>
            </NoumSectionContainer>
          ))}
        </Stack>
      </>
    </BodyContainer>
  );
};
