import React, { useLayoutEffect, useMemo, useRef, useState } from 'react';
import { Element } from '@/screens/Chamber/components/Element';
import { useSkeletonIsLoadingContext } from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import { SkeletonLoaderElements } from '@/screens/Chamber/components/elements/SkeletonLoader/const';
import {
  ConnectionRequestTypeEnum,
  ElementTypeEnum,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly } from '@/hooks';
import { useNoumContext } from './ChamberProvider';
import { type NoumViewElementProps } from './types';
import { ElementWrapper } from './styles';
import { toolRenderHelper } from './noumViewHelper';

const NoumViewElement: React.FC<NoumViewElementProps> = ({
  tools,
  isCompleteLoading,
  elementId,
}) => {
  const columnRef = useRef<HTMLDivElement>(null);
  const { isLoading } = useSkeletonIsLoadingContext();
  const {
    spaceId,
    space,
    isOwner,
    isFollowing,
    noumType,
    mainSpaceId,
    isMasterNoum,
    connectionStatus,
    isConnected,
  } = useNoumContext();
  const { user, isUnregistered } = useAuth();
  const { flags } = useLaunchDarkly();
  const [containerWidth, setContainerWidth] = useState<number>();
  const { renderableTools } = toolRenderHelper(
    space,
    isConnected,
    tools,
    user!,
    isUnregistered,
    flags,
  );

  const updatedList = useMemo(
    () =>
      noumType === SpaceTypeEnum.Project
        ? renderableTools()
        : renderableTools().filter(
            (element) =>
              isLoading ||
              (isFollowing &&
                [
                  ElementTypeEnum.Userposts,
                  ElementTypeEnum.QuickQuestions,
                ].includes(element.elementType as ElementTypeEnum)),
          ),
    [isFollowing, isLoading, noumType, renderableTools],
  );

  const conditionalRender = useMemo(
    () =>
      spaceId !== mainSpaceId &&
      !isOwner &&
      !isMasterNoum &&
      connectionStatus !== ConnectionRequestTypeEnum.Approved,
    [connectionStatus, isMasterNoum, isOwner, mainSpaceId, spaceId],
  );

  const list = isLoading
    ? SkeletonLoaderElements
    : conditionalRender
    ? updatedList
    : renderableTools();

  useLayoutEffect(() => {
    setContainerWidth(columnRef.current?.clientWidth);
  }, [containerWidth, columnRef, list]);

  return (
    <>
      {list &&
        list.map((tool) => (
          <ElementWrapper
            ref={columnRef}
            key={`${tool._id}`}
            isBackgroundContent={
              tool.elementType === ElementTypeEnum.Video ||
              tool.elementType === ElementTypeEnum.Image ||
              tool.elementType === ElementTypeEnum.Text
            }
          >
            <Element
              isHighlight={isCompleteLoading && tool._id === elementId}
              isEditing={false}
              spaceId={spaceId || ''}
              spaceType={space?.type || ''}
              isOwner={isOwner}
              element={tool}
              id={`${tool._id}`}
              currentTitle={tool.headerContent ?? tool.elementType ?? 'UNKNOWN'}
              columnWidth={containerWidth}
              isNoumEditor2
            />
          </ElementWrapper>
        ))}
    </>
  );
};

export default NoumViewElement;
