import { type FC, useCallback, useEffect, useMemo, useState } from 'react';
import { type ApolloError } from '@apollo/client';
import * as Sentry from '@sentry/react';
import { t } from 'i18next';
import { useSearchParams } from 'react-router-dom';
import { Element } from '@/screens/Chamber/components/Element';
import { Spacer } from '@/layout';
import {
  BodyContentEnum,
  ConnectionRequestTypeEnum,
  ElementTypeEnum,
  type Maybe,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { useSkeletonIsLoadingContext } from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import { SkeletonLoaderElements } from '@/screens/Chamber/components/elements/SkeletonLoader/const';
import { useMarkNoumAsRecentMutation } from '@/apollo/graphql';
import { useToast } from '@/hooks';
import {
  type Questions,
  useRiseQuestions,
} from '@/features/noums/hooks/spaceQuery';
import { BodyContainer } from './styles';
import { useNoumContext } from './ChamberProvider';
import { RiseEssayQuestion } from '../components/RiseApplicationQuestion/RiseEssayQuestion';
import { RiseApplicationReadMe } from '../components/RiseApplicationReadMe';

export const ChamberBody: FC = () => {
  const {
    spaceId,
    space,
    connectionStatus,
    mainSpaceId,
    elements,
    isOwner,
    isArchived,
    isFollowing,
    isMasterNoum,
    noumType,
    loading,
    loadingSpace,
  } = useNoumContext();
  const { isLoading } = useSkeletonIsLoadingContext();
  const [markNoumAsRecent] = useMarkNoumAsRecentMutation();
  const [riseEssays, setRiseEssays] =
    useState<Maybe<(Questions | null | undefined)[]>>();
  const { addToast } = useToast();

  const [searchParams] = useSearchParams();
  const elementId = searchParams.get('elementId');

  const isCompleteLoading = useMemo(
    () =>
      !loading &&
      !loadingSpace &&
      !!space?._id &&
      document.querySelectorAll<HTMLSpanElement>(`[data-testid="spinner"]`)
        .length < 1,
    [loading, loadingSpace, space?._id],
  );

  const {
    questions,
    applicationId,
    resultJson,
    refetch,
    status,
    parentNoumId,
  } = useRiseQuestions(spaceId!);

  useEffect(() => {
    if (elementId && elementId !== '') {
      if (isCompleteLoading) {
        const element = document.getElementById(elementId);
        if (element) {
          setTimeout(() => {
            element.scrollIntoView({ block: 'center', behavior: 'smooth' });
          }, 3000);
        }
      }
    } else
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
  }, [elementId, isCompleteLoading, loading, loadingSpace]);

  useEffect(() => {
    if (
      noumType === SpaceTypeEnum.RiseApplication &&
      questions &&
      questions?.length > 0
    ) {
      setRiseEssays(questions);
    }
  }, [noumType, questions]);

  const conditionalRender = useMemo(
    () =>
      spaceId !== mainSpaceId &&
      !isOwner &&
      !isMasterNoum &&
      connectionStatus !== ConnectionRequestTypeEnum.Approved,
    [connectionStatus, isMasterNoum, isOwner, mainSpaceId, spaceId],
  );

  const handleError = useCallback(
    (error: ApolloError) => {
      const { networkError, graphQLErrors } = error;
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${
          error ? error.message : networkError
        }`,
      );
      const [err] = graphQLErrors;
      Sentry.captureException(new Error(err?.message ?? networkError), {
        tags: {
          section: 'markNoumAsRecent',
        },
      });
    },
    [addToast],
  );

  const handleMarkNoumasRecent = useCallback(
    async (noumId) => {
      await markNoumAsRecent({
        variables: { noumId },
        onError: (error) => {
          handleError(error);
        },
      });
    },
    [handleError, markNoumAsRecent],
  );

  useEffect(() => {
    if (spaceId) handleMarkNoumasRecent(spaceId);
  }, [handleMarkNoumasRecent, spaceId]);

  const list = isLoading ? SkeletonLoaderElements : elements;
  const updatedList = useMemo(
    () =>
      noumType === SpaceTypeEnum.Project
        ? list
        : list.filter(
            (element) =>
              isLoading ||
              (isFollowing &&
                [
                  ElementTypeEnum.Userposts,
                  ElementTypeEnum.QuickQuestions,
                ].includes(element.elementType as ElementTypeEnum)),
          ),
    [isFollowing, isLoading, list, noumType],
  );

  if (conditionalRender) {
    return (
      <BodyContainer disabled={isArchived}>
        {updatedList.map((element) => (
          <div key={`${element._id}`}>
            <Spacer height={16} />
            <Element
              isHighlight={isCompleteLoading && element._id === elementId}
              isEditing={false}
              spaceId={spaceId || ''}
              isOwner={isOwner}
              element={element}
              id={`${element._id}`}
              currentTitle={
                element.headerContent ?? element.elementType ?? 'UNKNOWN'
              }
            />
          </div>
        ))}
      </BodyContainer>
    );
  }

  const riseElement = {
    bodyContentJson: null,
    bodyContentType: BodyContentEnum.Text,
    elementType: 'TEXT',
    status: 'PUBLISHED',
  };

  return (
    <BodyContainer disabled={isArchived}>
      {noumType === SpaceTypeEnum.RiseApplication && (
        <>
          <Spacer height={16} />
          <RiseApplicationReadMe />
        </>
      )}
      {noumType === SpaceTypeEnum.RiseApplication &&
        riseEssays &&
        riseEssays.map((object) => (
          <div key={`${object?.id}`}>
            <Spacer height={16} />
            <RiseEssayQuestion
              currentTitle={object?.question || ''}
              isEditing={false}
              spaceId={spaceId || ''}
              isOwner={isOwner}
              element={riseElement}
              applicationId={applicationId}
              questionId={object?.id}
              refetch={refetch}
              resultJson={resultJson}
              status={status}
              parentNoumId={parentNoumId}
            />
          </div>
        ))}
      {list.map((element) => (
        <div key={`${element._id}`}>
          <Spacer height={16} />
          <Element
            isHighlight={isCompleteLoading && element._id === elementId}
            isEditing={false}
            spaceId={spaceId || ''}
            spaceType={space?.type || ''}
            isOwner={isOwner}
            element={element}
            id={`${element._id}`}
            currentTitle={
              element.headerContent ?? element.elementType ?? 'UNKNOWN'
            }
          />
        </div>
      ))}
    </BodyContainer>
  );
};
